import React, {useEffect, useState,useRef} from 'react';
import data from './db/firstjson';
import crypto from 'crypto';

function Room({match}) {

    const [question,setQuestion] = useState(['loading','loading','loading']);
    const questionRef = useRef(question);
    questionRef.current = question;
    useEffect(() => {
       for(let i = 0; i<data.length; i++){
           console.log('forloop');
           crypto.pbkdf2(match.params.key,'greatroomescape',213510,64,'sha512',(err,key)=>{
                   if(key.toString('base64')===data[i].key){
                       setQuestion(Object.values(data[i]));
                   }
           })
       }
       setTimeout(()=>{
           console.log(question);
           console.log(questionRef.current);
           if(questionRef.current[0] === 'loading' ){
                setQuestion(['error','error','error']);
           }
       },5000)
    },[]);


    return (
        <div>
            <h1>{question[0]}.</h1>
            {question[2]}
        </div>
    );
}

export default Room;